import service from '../../service/service';
import Loading from '@/components/common/loading/index.vue';
import lrz from 'lrz';
export default {
  components: {
    Loading
  },
  data() {
    return {
      isShowLoading: false,
      query: {},
      uploadReason: ''
    };
  },
  created() {
    // const query = {};
    // const aParams = document.location.search.substr(1).split('&');
    // for (let i = 0; i < aParams.length; i++) {
    //   const arr = aParams[i].split('=');
    //   query[arr[0]] = arr[1];
    // }
    const query = this.$route.query || {};
    this.query = query;
    // console.log(this.query);
    this.isShowLoading = true;
    let formdata = new FormData();
    formdata.append('msg', query.msg);
    formdata.append('sign', query.sign);
    formdata.append('appkey', query.appkey);
    service.testQueryStatus(formdata).then(res => {
      console.log(res);
      this.isShowLoading = false;
      if (res && res.data && (res.data.retCode === 'S')) {
      } else {
        const reason = res && res.data && res.data.retMsg || '';
        this.$router.push({name: 'failed', params: {reason: reason}});
      }
    }, () => {
      this.$router.push('failed');
    });
  },
  mounted() {
  },
  methods: {
    async handleFileChange(event) {
      let file = event.target.files[0];
      console.log(file);
      if (file.size > (30 * 1024 * 1024)) {
        this.uploadReason = '请重新拍摄3~5秒的视频';
        return;
      }
      // 打印压缩前的图片信息
      console.log(new Date().getTime());
      // alert(file.size);
      console.log('压缩前', file.size / 1024);
      // 压缩
      let lrzResult = await this.lrzToLimit(file, 200 * 1024);
      // 把blob转化成file
      let newfile = new window.File([lrzResult.file], file.name, {
        type: file.type
      });
      console.log('压缩后', newfile.size / 1024);

      this.uploadFaceVideo(newfile);
    },
    backAction() {
      window.history.go(-1);
    },
    uploadFaceVideo(file) {
      let formdata = new FormData();
      formdata.append('msg', this.query.msg);
      formdata.append('sign', this.query.sign);
      formdata.append('file', file);
      formdata.append('appkey', this.query.appkey);
      this.isShowLoading = true;
      service.uploadFaceVideo(formdata).then(res => {
        this.isShowLoading = false;
        if (res && res.data && res.data.retCode && (['0', '1'].includes(res.data.retCode))) {
          this.uploadReason = res.data.retMsg;
          if (res.data.fronturl) {
            const str = 'retcode=' + res.data.retCode + '&retmsg=' + res.data.retMsg;
            if (res.data.fronturl.includes('?')) {
              setTimeout(() => {
                window.location.href = res.data.fronturl + '&' + str + '&time=' + (new Date().getTime());
              }, 300);
            } else {
              setTimeout(() => {
                window.location.href = res.data.fronturl + '?' + str + '&time=' + (new Date().getTime());
              }, 300);
            }
          }
          // this.showToast(res.data.retMsg);
        } else {
          const reason = res && res.data && res.data.retMsg || '上传失败';
          this.uploadReason = reason;
          // this.showToast(reason);
        }
      }, () => {
        this.isShowLoading = false;
        this.uploadReason = '上传失败';
        // this.showToast('上传失败');
      });
    },
    // 将文件压缩至指定大小 默认500KB
    async lrzToLimit(orgFile, fileLimitSize) {
      // 限制大小500kb
      let limitSize = !fileLimitSize || fileLimitSize === 0 ? 500 * 1024 : fileLimitSize;
      let beginWidth = 1280;
      let dest = '';
      if (orgFile.size <= limitSize) {
        const windowURL = window.URL || window.webkitURL;
        let url = windowURL.createObjectURL(orgFile);
        dest = {
          file: orgFile,
          base64: url
        };
      } else {
        dest = await lrz(orgFile, {quality: 1});
      }
      for (let index = 0; index < 10; index++) {
        if (dest.file.size <= limitSize) {
          // 符合压缩大小
          return dest;
        }
        dest = await lrz(orgFile, {width: beginWidth, quality: 1});
        beginWidth = beginWidth * 0.75;
      }
      return dest;
    }
  }
};
