import service from '../../../service/service';
import Loading from '@/components/common/loading/index.vue';
import {RecordRTCPromisesHandler}  from 'recordrtc';
import ysFixWebmDuration from 'fix-webm-duration';
export default {
  components: {
    Loading
  },
  data() {
    return {
      isShowLoading: false,
      isShowModal: false,
      query: {},
      uploadReason: '',
      numberArr: [],
      isBegin: false,
      timer: null,
      countDownCode: 3,
      isShowTip: true
    };
  },
  created() {

  },
  mounted() {
  },
  methods: {
    backAction() {
      window.history.go(-1);
    },
    uploadFaceVideo(file) {
      let formdata = new FormData();
      formdata.append('msg', this.query.msg);
      formdata.append('sign', this.query.sign);
      formdata.append('file', file);
      formdata.append('appkey', this.query.appkey);
      this.isShowLoading = true;
      service.uploadFaceVideo(formdata).then(res => {
        this.isShowLoading = false;
        if (res && res.data && res.data.retCode && (['0', '1', '2'].includes(res.data.retCode))) {
          this.uploadReason = res.data.retMsg;
          if (res.data.fronturl) {
            const str = 'retcode=' + res.data.retCode + '&retmsg=' + res.data.retMsg;
            if (res.data.fronturl.includes('?')) {
              window.location.href = res.data.fronturl + '&' + str + '&time=' + (new Date().getTime());
            } else {
              window.location.href = res.data.fronturl + '?' + str + '&time=' + (new Date().getTime());
            }
          }
          // this.showToast(res.data.retMsg);
        } else {
          const reason = res && res.data && res.data.retMsg || '上传失败';
          this.uploadReason = reason;
          this.showToast(reason);
        }
      }, () => {
        this.isShowLoading = false;
        this.uploadReason = '上传失败';
        this.showToast('上传失败');
      });
    },
    sure() {
      this.isShowModal = false;
    },
    async handleTakeBtn() {
      this.isShowTip = true;
      // 设置视频分辨率
      try {
        const constraints = {audio: false, video: {facingMode: 'user', height: '720', width: '1280'}};
        const stream = await navigator.mediaDevices.getUserMedia(constraints);
        const video = this.$refs.video;
        video.style.width = document.width + 'px';
        video.style.height = document.height + 'px';
        video.oncanplay = async () => {
          console.log('分辨率：' + video.videoWidth + ' ' + video.videoHeight);
          this.isBegin = true;
          let recorder = new RecordRTCPromisesHandler(stream, {
            type: 'video'
          });
          const sleep = m => new Promise(r => setTimeout(r, m));
          for (let i = 6; i > 0; i--) {
            if (i === 3) {
              await recorder.startRecording();
              this.isShowTip = false;
            }
            this.countDownCode = i;
            await sleep(1000);
          }
          debugger;
          // 停止拍摄
          await recorder.stopRecording();
          let blob = await recorder.getBlob();
          // 关闭摄像头
          this.isBegin = false;
          stream.getTracks().forEach((track) => {
            track.stop();
          });
          // 修复时长
          let fixedBlob = await ysFixWebmDuration(blob, 3000);
          const file = new File([fixedBlob], 'video.mp4', {
            type: 'video/mp4'
          });
          this.uploadFaceVideo(file);
        };
        video.srcObject = stream;
      } catch (err) {
        console.log(err.message);
        if (err.message.indexOf('denied') !== -1) {
          this.showToast('摄像头未授权');
        }
      }
    }
  }
};
