import Vue from 'vue';
import Router from 'vue-router';
import setTitleHack from '../utils/mixins/setTitleHack';
import IndexCom from '../components/index/index.vue';
import FailedCom from '../components/failed/failed.vue';
import FaceComVideo from '../components/video/index.vue';

Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: '/',
      redirect: '/facecom'
    },
    {
      path: '/facecom',
      name: 'facecom',
      component: IndexCom,
      meta: {
        title: '人脸识别'
      }
    },
    {
      path: '/facecomvideo',
      name: 'facecomvideo',
      component: FaceComVideo,
      meta: {
        title: '人脸识别'
      }
    },
    {
      path: '/failed',
      name: 'failed',
      component: FailedCom,
      meta: {
        title: '人脸识别'
      }
    }
  ]
});

// 针对于路由跳转相同的地址添加catch捕获异常
const originalPush = Router.prototype.push;
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err);
};

router.beforeEach((to, from, next) => {
  setTitleHack.methods.setTitleHack(to.meta.title);
  next();
});

export default router;
