import service from '../../../service/service';
import Loading from '@/components/common/loading/index.vue';
export default {
  components: {
    Loading
  },
  data() {
    return {
      isShowLoading: false,
      isShowModal: false,
      query: {},
      uploadReason: '',
      numberArr: []
    };
  },
  created() {
    this.getRandomNum();
    const query = this.$route.query || {};
    this.query = query;
    console.log(this.query);
    this.isShowLoading = true;
    let formdata = new FormData();
    formdata.append('msg', query.msg);
    formdata.append('sign', query.sign);
    formdata.append('appkey', query.appkey);
    service.testQueryStatus(formdata).then(res => {
      console.log(res);
      this.isShowLoading = false;
      if (res && res.data && (res.data.retCode === 'S')) {
      } else {
        const reason = res && res.data && res.data.retMsg || '';
        this.$router.push({name: 'failed', params: {reason: reason}});
      }
    }, () => {
      this.$router.push('failed');
    });
  },
  mounted() {
  },
  methods: {
    async handleFileChange(event) {
      let file = event.target.files[0];
      console.log(file);
      // if (file.size > (10 * 1024 * 1024)) {
      //   this.uploadReason = '请重新拍摄1~2秒的视频';
      //   return;
      // }
      // 打印压缩前的图片信息
      console.log(new Date().getTime());
      // alert(file.size);
      console.log('视频大小', file.size / 1024);
      let videoDuration = await this.getVideoDuration(file);
      console.log('视频时长', videoDuration);
      if (videoDuration < 2) {
        this.uploadReason = '拍摄时间小于3秒，请重新拍摄3~4秒的视频';
        this.isShowModal = true;
        return;
      }
      if (videoDuration > 5) {
        this.uploadReason = '拍摄时间大于4秒，请重新拍摄3~4秒的视频';
        this.isShowModal = true;
        return;
      }
      this.uploadFaceVideo(file);
    },
    backAction() {
      window.history.go(-1);
    },
    uploadFaceVideo(file) {
      let formdata = new FormData();
      formdata.append('msg', this.query.msg);
      formdata.append('sign', this.query.sign);
      formdata.append('file', file);
      formdata.append('appkey', this.query.appkey);
      this.isShowLoading = true;
      service.uploadFaceVideo(formdata).then(res => {
        this.isShowLoading = false;
        if (res && res.data && res.data.retCode && (['0', '1', '2'].includes(res.data.retCode))) {
          this.uploadReason = res.data.retMsg;
          if (res.data.fronturl) {
            const str = 'retcode=' + res.data.retCode + '&retmsg=' + res.data.retMsg;
            if (res.data.fronturl.includes('?')) {
              window.location.href = res.data.fronturl + '&' + str + '&time=' + (new Date().getTime());
            } else {
              window.location.href = res.data.fronturl + '?' + str + '&time=' + (new Date().getTime());
            }
          }
          // this.showToast(res.data.retMsg);
        } else {
          const reason = res && res.data && res.data.retMsg || '上传失败';
          this.uploadReason = reason;
          this.showToast(reason);
          this.getRandomNum();
        }
      }, () => {
        this.isShowLoading = false;
        this.uploadReason = '上传失败';
        this.showToast('上传失败');
        this.getRandomNum();
      });
    },
    getVideoDuration(file) {
      return new Promise((resolve) => {
        // 获取视频或者音频时长
        const fileurl = URL.createObjectURL(file);
        // 经测试，发现audio也可获取视频的时长
        const audioElement = new Audio(fileurl);
        // 静音
        audioElement.muted = true;
        // ios 微信里只有调用play方法才触发loadedmetadata事件
        audioElement.play().then(() => audioElement.pause());
        // loadedmetadata 视频源数据加载成功
        audioElement.addEventListener('loadedmetadata',  (_event) => {
          const duration = audioElement.duration; // 视频时长
          resolve(duration);
        });
      });
    },
    getRandomNum() {
      this.numberArr = [];
      for (let i = 0; i < 4; i++) {
        let num = parseInt(Math.random() * 9);
        this.numberArr.push(num);
      }
    },
    sure() {
      this.isShowModal = false;
      this.getRandomNum();
    }
  }
};
