
function toast (duration, message){
  const modalDom = document.createElement('div');
  const time =  duration * 1000 || 3000;
  modalDom.innerHTML = message;
  modalDom.className = 'toast';
  document.body.appendChild(modalDom);
  setTimeout(() => {
    document.body.removeChild(modalDom);
  }, time);
};
export default {
  install: function (Vue) {
    Vue.prototype.showToast = function(message){
      Vue.prototype.toast(2.0, message);
    };
    Vue.showToast = function(message){
      Vue.toast(2.0, message);
    };
    Vue.toast = toast;
    Vue.prototype.toast = toast;
  }
};
